<template>
	<view class="detail">
		<swiper :indicator-dots="true" :autoplay="true" :interval="3000" :duration="1000">
			<swiper-item v-for="(item,index) in detailData.swiperList" :key="index">
				<view class="swiper-item">
					<image class="swiper-img" :src="item.imgUrl" alt="">
				</view>
			</swiper-item>
		</swiper>
		
		<view class="detail-good">
			<view class="good-name">{{detailData.detailName}}</view>
			<view class="good-price">
				<view class="good-nprice">￥{{detailData.detailNPrice}}</view>
				<view class="good-oprice">￥{{detailData.detailOPrice}}</view>
			</view>

		</view>
		
		<view class="detail-img">
			<view v-for="(item,index) in detailData.detailImg" :key="index">
				<image :src="item" mode=""></image>
			</view>
		</view>
		
		<view class="detail-footer">
			<view class="iconfont icon-xiaoxi"></view>
			<view class="iconfont icon-gouwuche"></view>
			<view class="add-shopcart">加入购物车</view>
			<view class="buy" @tap="showPop">立即购买</view>
		</view>
		
		<view class="pop" v-show="isShow" @touchmove.stop.prevent="">
			<view class="pop-mask"  @tap="hidePop"></view>
			<view class="pop-box" :animation="animationData">
				<view>
					<image class="pop-img" src="../../static/image/five.jpeg" mode=""></image>
				</view>
				<view class="pop-count">
					<view>购买数量</view>
					<NumberBox></NumberBox>
				</view>
				<view class="pop-btn" >确定</view>
			</view>
		</view>
	</view>
</template>

<script>
	import NumberBox from '@/common/uni/uni-number-box/uni-number-box.vue'
	export default {
		comments:{
			NumberBox
		},
		data() {
			return {
				detailData:{
					detailName:'2021年新款夏天港风复古chic休闲时尚网红炸街衣服短裤两件套装女',
					detailOPrice:300.00,
					detailNPrice:200.00,
					swiperList:[
						{imgUrl:'/static/image/one.jpeg'},
						{imgUrl:'/static/image/one.jpeg'},
						{imgUrl:'/static/image/one.jpeg'},
					],
					detailImg:[
						'/static/image/six.jpeg',
						'/static/image/five.jpeg',
						'/static/image/four.jpeg',
					]					
				},
				isShow:false,
				animationData:{}

			}
		},
		methods: {
			showPop(){
				this.isShow=!this.isShow;
				var animation = uni.createAnimation();
				
				animation.height(300).step();
				this.animationData=animation.export();
				
				// setTimeout(()=>{
				// 	animation.height(200).step();
				// 	this.animationData=animation.export();
				// },1000/60)
					
			},
			hidePop(){
				var animation = uni.createAnimation();
				animation.height(0).step();
				this.animationData=animation.export();
				
				this.isShow=!this.isShow;
			}
		}
	}
</script>

<style scoped>
.detail{
	padding-bottom: 90rpx;
}
::-webkit-scrollbar{
	display: none;
}
.swiper-img{
	width: 100%;
	height: 700rpx;
}
.good-name{
	display:-webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
	text-overflow: ellipsis;
	overflow: hidden;
}
.detail-good{
	padding:10rpx 0;
}
.good-price{
	display: flex;
	align-items: center;
}
.good-nprice{
	margin-right:10rpx;
	font-size: 38rpx;
	color:red;
}
.good-oprice{
	align-self: flex-end;
	text-align: center;
	text-decoration: line-through;
}
.detail-img image{
	width: 100%;
	display: block;
}
.detail-footer{
	position: fixed;
	left:0;
	bottom: 0;
	width: 100%;
	height: 90rpx;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	background-color: #fff;
}
.iconfont{
	width: 60rpx;
	height: 60rpx;
	line-height: 60rpx;
	border-radius: 100%;
	background-color: #000;
	color:#fff;
	text-align: center;
	margin:0 5rpx;
}
.add-shopcart{
	color:#fff;
	background-color: #000;
	border-radius: 40rpx;
	padding: 6rpx 20rpx ;
}
.buy{
	color:#fff;
	background-color: #49bdfe;
	border-radius: 40rpx;
	padding: 6rpx 20rpx ;
}
.pop{
	position: fixed;
	top:0;
	left: 0;
	z-index: 99;
	width: 100%;
	height: 100%;
}
.pop-mask{
	position: absolute;
	left: 0;
	bottom:0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.3);
}
.pop-box{
	position: absolute;
	left: 0;
	bottom: 90rpx;
	width: 100%;
	height: 0;
	background-color: #fff;
}
.pop-img{
	width: 260rpx;
	height: 260rpx;
}
.pop-count{
	padding: 20rpx;
}
.pop-btn{
	width: 100%;
	line-height: 80rpx;
	background-color: #49bdfe;
	color:#fff;
	text-align: center;
	position: absolute;
	bottom: 0;
}
</style>
